import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  notes: [],
  activeNote: {}
}

const getters = {
  activeNote: state => {
    return state.activeNote
  },
  notes: state => state.notes,
  activeNoteText: state => state.activeNote.text
}

const mutations = {
  addNote (state) {
    const newNote = {
      text: 'New note',
      favorite: false
    }
    state.notes.push(newNote)
    state.activeNote = newNote
  },

  editNote (state, text) {
    state.activeNote.text = text
  },

  deleteNote (state) {
    state.notes.splice(state.notes.indexOf(state.activeNote), 1)
    state.activeNote = state.notes[0] ? state.notes[0] : {};
  },

  toggleFavorite (state) {
    state.activeNote.favorite = !state.activeNote.favorite
  },

  setActiveNote (state, note) {
    state.activeNote = note
  }
}

const actions = {
  addNote (context) {
    context.commit('addNote')
  },

  editNote (context, e) {
    context.commit('editNote', e.target.value)
  },

  deleteNote (context) {
    context.commit('deleteNote')
  },

  updateActiveNote (context, note) {
    context.commit('setActiveNote', note)
  },

  toggleFavorite (context) {
    context.commit('toggleFavorite')
  }
}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})



