<template>
  <div id="note-editor">
    <textarea
      :value="activeNoteText()"
      @input="editNote"
      class="form-control">
    </textarea>
  </div>
</template>

<script>
// import { editNote } from '../vuex/actions'
import { mapActions, mapGetters } from 'vuex'

export default {
  // computed: {
  //   ...mapGetters([
  //     'activeNoteText'
  //   ])
  // },
  methods: {
    ...mapActions([
      'editNote'
    ]),
    activeNoteText () {
      return this.$store.getters.activeNoteText
    }
  }
}
</script>



